@import url(https://fonts.googleapis.com/css2?family=Teko:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #181818;
  color: white;
  font-family: Teko;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  letter-spacing: 0.03em;
}

.app__greeting {
    width: 100%;
    min-height: 100vh;
    height: 100vh;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.greeting__hintBox {
    position: relative;
    width: 312px;
    height: 100%;
    display: flex;
    align-items: center;
}

.greeting__accent {
    color: #F0DB4F;
}

.greeting__accentJoke {
    text-decoration: line-through;
}

.textBox__wrapper {
    margin-bottom: 48px;
}

.greeting__graph {
    position: relative;
    width: 531px;
    height: 100%;
    display: flex;
    align-items: center;
}

.graph__doughnut {
    position: relative;
    width: 100%;
    height: 531px;
}

.doughnut__heading {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    font-family: Teko;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    text-align: center;
    letter-spacing: 0.03em;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    pointer-events: none;
}

.doughnut__bigHeading {
    font-size: 56px;
}

.doughnut__headingWrapper {
    width: 100%;
}

.doughnut__option {
    position: absolute;
    cursor: pointer;
}

.doughnut__optionLogo {
    display: flex;
    width: 100px;
    margin-bottom: 5px;
}

.svgTechIcon{
    transition: .5s;
    transition-delay: 0s;
}

.svgTechIcon_chosen {
    -webkit-filter: drop-shadow(0px 0px 5px);
            filter: drop-shadow(0px 0px 5px);
    transition-delay: 0s;
    transition: .5s;
}

.doughnut__optionLogoContent {
    width: 32px !important;
    text-align: center;
}

.doughnut__optionLogoWrapper {
    width: 100%;
    display: flex;
    justify-content: center;
}

.doughnut__optionLogoContent {
    width: 32px;
    font-size: 32px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .5s;
    opacity: .3;
}

.doughnut__optionName {
    width: 100px;
    font-size: 24px;
    text-align: center;
    opacity: .3;
    transition: .5s;
}

.doughnut__optionName_chosen {
    opacity: 1;
    transition: .5s;
}

.hintBox__hint {
    position: absolute;
    width: 312px;
    height: -webkit-max-content;
    height: max-content;
    /* top: 0;
    left: 0; */
    font-family: 'Indie Flower', cursive;
    font-size: 24px;
    transition: .2s;
    transition-timing-function: ease-in-out;
    transition-delay: .1s;
    cursor: default;
    -webkit-animation: shaker 5s linear 0s infinite alternate;
            animation: shaker 5s linear 0s infinite alternate;
    display: flex;
    align-items: center;
    text-indent: -20px;
}

/* .doughnut__optionAnimation {
    animation: shaker 5s linear 0s infinite alternate;
} */

.hintBox__text {
    position: absolute;
}

.textBox__isometric {
    /* height: 200px; */
    margin-top: 90px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.textBox__isometric a {
    position: relative;
    display: block;
    width: 80px;
    height: 100px;
    /* transform: perspective(1000px) rotateX(15deg) skewY(-15deg); */
    text-decoration: none;
    color: #242424;
}

.textBox__isometric a:hover .isometric__content {
    -webkit-transform: translate(-25px, -35px);
            transform: translate(-25px, -35px);
    transition: .3s;
}

.isometric__content {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: #fff;
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    transition: .3s;
}

.isometric__link {
    margin: 0 25px;
}

.isometric__linkName {
    position: relative;
    z-index: 0;
    color: var(--pseudoColor);
    opacity: 1;
    text-align: center;
    font-size: 36px;
    -webkit-transform: skewX(45deg) translate(65px, -70px);
            transform: skewX(45deg) translate(65px, -70px);
    letter-spacing: 0.03em;
}

.isometric__content::before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100%;
    width: 20px;
    background: var(--pseudoColor);
    -webkit-transform: skewY(45deg) translate(-20px, 10px);
            transform: skewY(45deg) translate(-20px, 10px);
}

.isometric__content::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 20px;
    width: 100%;
    background: var(--pseudoColor);
    -webkit-transform: skewX(45deg) translate(10px, -20px);
            transform: skewX(45deg) translate(10px, -20px);
}

.graph__cell {
    transition: .5s;
    cursor: pointer;
}

@-webkit-keyframes shaker {
    0% {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
        /* text-shadow: 0px 0px 0px; */
    }

    10% {
        -webkit-transform: translate(-1px, -1px);
                transform: translate(-1px, -1px);
    }

    20% {
        -webkit-transform: translate(-1px, 0px);
                transform: translate(-1px, 0px);
    }

    30% {
        -webkit-transform: translate(0px, 1px);
                transform: translate(0px, 1px);
    }

    40% {
        -webkit-transform: translate(2px, 2px);
                transform: translate(2px, 2px);
    }

    50% {
        -webkit-transform: translate(1px, 0px);
                transform: translate(1px, 0px);
        /* text-shadow: 0px 0px 5px; */
    }

    60% {
        -webkit-transform: translate(-1px, -2px);
                transform: translate(-1px, -2px);
    }

    70% {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
    }

    80% {
        -webkit-transform: translate(-2px, -2px);
                transform: translate(-2px, -2px);
    }

    90% {
        -webkit-transform: translate(0px, -2px);
                transform: translate(0px, -2px);
    }

    100% {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
        /* text-shadow: 0px 0px 0px; */
    }
}

@keyframes shaker {
    0% {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
        /* text-shadow: 0px 0px 0px; */
    }

    10% {
        -webkit-transform: translate(-1px, -1px);
                transform: translate(-1px, -1px);
    }

    20% {
        -webkit-transform: translate(-1px, 0px);
                transform: translate(-1px, 0px);
    }

    30% {
        -webkit-transform: translate(0px, 1px);
                transform: translate(0px, 1px);
    }

    40% {
        -webkit-transform: translate(2px, 2px);
                transform: translate(2px, 2px);
    }

    50% {
        -webkit-transform: translate(1px, 0px);
                transform: translate(1px, 0px);
        /* text-shadow: 0px 0px 5px; */
    }

    60% {
        -webkit-transform: translate(-1px, -2px);
                transform: translate(-1px, -2px);
    }

    70% {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
    }

    80% {
        -webkit-transform: translate(-2px, -2px);
                transform: translate(-2px, -2px);
    }

    90% {
        -webkit-transform: translate(0px, -2px);
                transform: translate(0px, -2px);
    }

    100% {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
        /* text-shadow: 0px 0px 0px; */
    }
}

.app__personal {
    height: 100vh;   
    display: flex;
    align-items: center;
    justify-content: center; 
}

.personal__awesomeBackground {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: #23D4AD;
    z-index: -1;
    opacity: 0;
    transition: opacity 1s;
    -webkit-animation: backgrounder 15s linear 0s infinite alternate;
            animation: backgrounder 15s linear 0s infinite alternate;

}

.personal__textBox {
    width: 312px;
    height: 100%;
    display: flex;
    align-items: center;
}

.textBox__wrapper {
    margin-bottom: 0px;
    -webkit-transform: perspective(10000px) rotateX(15deg) skewY(-15deg);
            transform: perspective(10000px) rotateX(15deg) skewY(-15deg);
}

.textBox__isometricWrapper {
    width: 240px;
    height: -webkit-max-content;
    height: max-content;
    padding-left: 20px;
}

.personal__isometricBackground {
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: #181818;
    border-radius: 20px;
    display: flex;
    justify-content: space-evenly;
}

.textBox__aside {
    width: 300px;
}

.personal h2 {
    position: relative;
    z-index: 1;
    font-size: 30px;
    font-style: normal;
    font-weight: normal;
    line-height: 52px;
    margin-left: 24px;
    letter-spacing: 0.03em;
    -webkit-transform: skewX(45deg);
            transform: skewX(45deg);
}

.isometric__frontPlane {
    -webkit-transform: skewX(0deg) !important;
            transform: skewX(0deg) !important;
    margin-left: -80px !important;
}

.isometricHeading {
    position: relative;
    width: 300px;
    margin-bottom: 40px;
}

.isometricHeading__decorator {
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50px;
    background: linear-gradient(90deg, #9A1AFF 0%, #E864CB 100%);
    -webkit-transform: skewX(45deg);
            transform: skewX(45deg);
}

.isometricHeading__decorator::before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100%;
    width: 20px;
    -webkit-transform: skewY(-45deg) translate(-20px, -10px);
            transform: skewY(-45deg) translate(-20px, -10px);
    background:#9A1AFF;
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
}

.isometricHeading__decorator::after {
    content: '';
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: 0;
    height: 20px;
    width: 100%;
    background: linear-gradient(90deg, #9A1AFF 0%, #E864CB 100%);
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
    -webkit-transform: skewX(-45deg) translate(10px, 20px);
            transform: skewX(-45deg) translate(10px, 20px);
}

