.isometric__skill {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.isometric__skillRatingWrapper {
    width: 150px;
    display: flex;
    justify-content: space-between;
}

.isometric__skillRating {
    position: relative;
    z-index: 1;
    width: 20px;
    height: 20px;
    background: #fff;
    font-size: 40px;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    transition: .3s;
}

.isometric__skillRating::before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100%;
    width: 5px;
    background:grey;
    -webkit-transform: skewY(45deg) translate(-5px, 2.5px);
            transform: skewY(45deg) translate(-5px, 2.5px);
}

.isometric__skillRating::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 5px;
    width: 100%;
    background:grey;
    -webkit-transform: skewX(45deg) translate(2.5px, -5px);
            transform: skewX(45deg) translate(2.5px, -5px);
}

.isometric__skillRating_active {
    background: #F0DB4F;
}

.isometric__skillRating_active::before, .isometric__skillRating_active::after {
    background: #F0DB4F;
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
}

@-webkit-keyframes backgrounder {
    0% {
        background: #23D4AD;
    }

    25% {
        background: #23A6D5;
    }

    75% {
        background: #E73C7E;
    }

    100% {
        background: #EE7752;
    }
}

@keyframes backgrounder {
    0% {
        background: #23D4AD;
    }

    25% {
        background: #23A6D5;
    }

    75% {
        background: #E73C7E;
    }

    100% {
        background: #EE7752;
    }
}

